  margin: 0 !important;
}
.right-arrow {
  background-image: url(data:image/png;base64,);
  background-size: cover;
  background-position: left;
  background-repeat: no-repeat;
  padding-left: 25px;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
