/* Lead Form Modal */
.lead-form-modal {
  z-index: 999999;
}
.lead-form-modal .modal-body {
  padding: 0;
}
.lead-form-modal .modal-content,
.lead-form-modal .modal-dialog {
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  min-width: 60%;
  border-radius: 5px;
  overflow: hidden;
}
.lead-form-modal .modal-body {
  background-color: rgba(0, 0, 0, 0);
  z-index: 2;
  position: relative;
