  right: 0;
  top: 0;
  color: #333;
  z-index: 9;
  font-weight: 200;
  padding: 10px 15px;
}
.modal-backdrop.in {
  opacity: 0.8;
}
.lead-form-modal .form-info {
  position: relative;
  z-index: 99;
  padding: 60px 30px;
}

